.header {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    background: black;
    color: white;
}
.header ul {
    display: flex;
    list-style: none;
}
.header li {
    margin: 0 15px;
}
.header a {
    color: white;
    text-decoration: none;
}
